.languages {
  display: flex;
  justify-content: center;
}
.languages li {
  margin: 10px;
  font-weight: bold;
  cursor: pointer;
}
.popular-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.popular-rank {
  font-size: 20px;
  margin: 10px;
}
.popular-item {
  margin: 20px;
  text-align: center;
}
.space-list-items {
  margin-bottom: 7px;
}
.avatar {
  width: 150px;
  border-radius: 50%;
}
